@import '../../style/mixin';

.footer {
  height: 320px;
  @include screenType2;
  background-color: #100516;
  .footerWrap {
    align-items: flex-end;
    @include screenType1;
    justify-content: space-between;
    .footerL {
      .logo {
        background: url(../../asset/img/icon.png);
        background-position: 0px -131px;
        width: 133px;
        height: 51px;
        margin: 0 0 20px 0;
      }
      p {
        @include w18l;
        margin: 0 0 10px 0;
      }
    }
    .footerR {
      text-align: end;
      .iconWrap {
        display: flex;
        justify-content: flex-end;
        margin-bottom: 20px;
        .icon {
          width: 31px;
          height: 31px;
          background-color: white;
          margin-left: 20px;
        }
        .face {
          background: url(../../asset/img/icon.png);
          background-position: -143px -235px;
        }
        .insta {
          background: url(../../asset/img/icon.png);
          background-position: 0 -235px;
        }
        .blog {
          background: url(../../asset/img/icon.png);
          background-position: -80px -235px;
        }
      }
      div {
        display: flex;
        justify-content: flex-end;
        p {
          @include w14r;
          margin: 0 0 10px 10px;
        }
      }
      p {
        @include w14r;
      }
    }
  }

  @include desktop {

    padding: 0 50px;
  }

  @include tablet {
    padding: 0 50px;
  }
  @include mobileF {
    padding: 0 30px;
    height: 400px;
    .footerWrap {
      flex-direction: column;
      align-items: flex-start;
      .footerL {
        p {
          @include w16r;
          margin: 0 0 10px 0;
        }
      }
      .footerR {
        text-align: end;
        margin-top: 30px;
        .iconWrap {
          justify-content: flex-start;
          .icon {
            margin: 0 20px 0 0;
          }
        }
        div {
          display: flex;
          justify-content: flex-start;
          p {
            @include w14r;
            margin: 0 10px 10px 0;
          }
        }
        p {
          @include w14r;
        }
      }
    }
    @include mobileS {
      padding: 0 20px;
    }
  }
}
