@import '../style/mixin';

.UsersPage {
  @include screenProtector;
  height: 100vh;
  background: linear-gradient(to bottom, #1a124a 50%, #433577);
  position: relative;
  .bgImg {
    width: 100%;
    height: 100vh;
    background: url(../asset/img/all_bg.png);
    .UsersWrap {
      width: 460px;
      padding: 0 20px;
      margin: 0 auto;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      div {
        .logo {
          width: 133px;
          height: 52px;
          background-image: url(../asset/img/icon.png);
          background-position: 0px -131px;
          margin: 0 auto 60px;
        }
        input {
          width: 100%;
          height: 56px;
          margin-bottom: 10px;
          padding: 0 20px;
        }
        .loginBtn {
          width: 100%;
          height: 56px;
          text-align: center;
          margin-top: 20px;
          border: 1px solid white;
          cursor: pointer;
          p {
            line-height: 56px;
            @include w16b;
          }
        }
        .kaLogin {
          border-radius: 0;
          border: none;
        }
        .kaLogin:hover {
          background-color: #fae300;
        }
        .join {
          display: flex;
          margin: 20px 0 0 0;
          p {
            margin: 0 10px 0 0;
            color: white;
            font-size: 14px;
            cursor: pointer;
          }
        }
        .address {
          text-align: center;
          margin-top: 60px;
          @include w14r;
        }
      }
    }
    .FooterImg {
      background-image: url(../asset/img/footerBg.png);
      width: 100%;
      height: 118px;
      background-size: cover;
      position: absolute;
      left: 0;
      bottom: 0;
      z-index: 55;
      background-size: cover;
      background-position: center;
    }
  }
}
.kaLogin {
  width: 100%;
  height: 56px;
  text-align: center;
  margin-top: 10px;
  background-color: #fae300;
  cursor: pointer;
  p {
    line-height: 56px;
    font-size: 16px;
    font-weight: bold;
    color: #111;
  }
}
