@import '../style/mixin';

.MembersPage {
  @include screenProtector;
  background: linear-gradient(to bottom, #1a124a 50%, #433577);
  height: 100vh;
  position: relative;
  .Bg {
    width: 100%;
    height: 100vh;
    background: url(../asset/img/all_bg.png);
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .UsersMemberWrap {
    height: 100vh;
    width: 460px;
    margin: 0 auto;
    // padding: 150px 20px 0 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    .logo {
      width: 132px;
      height: 52px;
      background: url(../asset/img/icon.png);
      background-position: 0px -131px;
      margin: 0 auto;
      margin-bottom: 60px;
    }
    .FoundTab {
      display: flex;
      div {
        height: 56px;
        width: 50%;
        border: 1px solid #fff;
        cursor: pointer;
        color: #fff;
        text-align: center;
        line-height: 56px;
        margin-bottom: 20px;
      }
      .tab.active {
        background-color: #33bbc5;
        border: none;
      }
    }
    .IdNumber {
      input {
        width: 100%;
        height: 56px;
        margin-top: 10px;
        padding: 20px;
      }
    }
    button {
      margin-top: 30px;
      color: #fff;
      width: 100%;
      height: 56px;
      background: none;
      border: 1px solid #fff;
      font-size: 17px;
      font-weight: bold;
      border-radius: 0;
      cursor: pointer;
    }
    .PasswordRecovery {
      input {
        width: 100%;
        height: 56px;
        margin-top: 10px;
        padding: 20px;
      }
    }
  }
  .MembersPageFooter {
    width: 100%;
    height: 131px;
    background: url(../asset/img/footerWeb.png) no-repeat center;
    position: relative;
    bottom: 0;
    background-size: cover;
  }
}
